import { Record, Union } from "../fable_modules/fable-library.3.6.3/Types.js";
import { record_type, option_type, string_type, union_type } from "../fable_modules/fable-library.3.6.3/Reflection.js";

export class Operation extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["CreateOrder", "CancelOrder", "CloseOrder", "AddSignatory", "AddSignatories"];
    }
    toString() {
        const x = this;
        switch (x.tag) {
            case 1: {
                return "cancelOrder";
            }
            case 2: {
                return "closeOrder";
            }
            case 3: {
                return "addSignatory";
            }
            case 4: {
                return "addSignatories";
            }
            default: {
                return "createOrder";
            }
        }
    }
}

export function Operation$reflection() {
    return union_type("Signatures.Operation", [], Operation, () => [[], [], [], [], []]);
}

export class DocumentInfo extends Record {
    constructor(Title, Content, Reference) {
        super();
        this.Title = Title;
        this.Content = Content;
        this.Reference = Reference;
    }
}

export function DocumentInfo$reflection() {
    return record_type("Signatures.DocumentInfo", [], DocumentInfo, () => [["Title", string_type], ["Content", string_type], ["Reference", option_type(string_type)]]);
}

